@use '../../../../variables' as variables;

// === MIXINS ===
@mixin flex(
  $direction: row,
  $gap: 0,
  $align: stretch,
  $justify: space-between
) {
  display: flex;
  flex-direction: $direction;
  gap: $gap;
  align-items: $align;
  justify-content: $justify;
}

@mixin card-style(
  $radius: 16px,
  $padding-y: variables.$global-light-spacing-5,
  $padding-x: variables.$global-light-spacing-4,
  $gap: variables.$global-light-spacing-5
) {
  @include flex(column, $gap);
  background-color: variables.$global-light-gray-gray-1;
  border-radius: $radius;
  padding: $padding-y $padding-x;
  filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.06));
}
:host {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow: visible;
  width: 100%;

  container-type: inline-size;
  container-name: main-layout;
  // max-width: 480px;
}

// === LAYOUT ===

.card-border-gradient {
  padding: 1px;
  border-radius: 12px;
  background-image: var(--cardBorderGradient);
  // max-width: 780px;
}

.payment-method-card {
  @include card-style(12px);
  width: 100%;
  gap: variables.$global-light-spacing-3;

  background-image: var(
    --cardBackground,
    radial-gradient(at top left, #ffffff 0%, #ffffff 75%),
  );
  h6 {
    color: var(--neutralColor1, variables.$global-light-gray-gray-10);
  }
  .body1 {
    color: var(--neutralColor5);
  }
  .body2 {
    color: var(--neutralColor5);
  }
}

.page-title {
  @include flex(row, 0, center, space-between);
  padding: variables.$global-light-spacing-4 variables.$global-light-spacing-5;
  background-color: variables.$global-light-gray-gray-1;
}

.pt-left {
  @include flex(row, variables.$global-light-spacing-2, center);
  color: variables.$global-light-daybreak-blue-blue-10;
}

.pt-icon {
  transform: scale(1.4);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.payment-method-outer-container {
  @include flex(column, variables.$global-light-spacing-4, center);
  width: 100%;
  padding: variables.$global-light-spacing-4;
  background-color: variables.$global-light-gray-gray-2;
}

.pmc-top {
  h5 {
    color: var(--neutralColor1, variables.$global-light-gray-gray-10);
  }
}

.pmc-content {
  @include flex(column, variables.$global-light-spacing-2);
}

.pmc-cards-container {
  @include flex(row, variables.$global-light-spacing-1);
}
.pmc-card-img-container.rombo-logo {
  background-image: var(--paymentMethodRomboLogo);
  background-repeat: no-repeat;
  background-size: contain;
}
.pmc-card-img-container.epayco-logo {
  background-image: var(--paymentMethodEpaycoLogo);
  background-repeat: no-repeat;
  background-size: contain;
}
.pmc-card {
  @include flex(column, variables.$global-light-spacing-4);
  border: 1px solid var(--secondaryColor2, variables.$global-light-gray-gray-4);
  border-radius: 8px;
  padding: variables.$global-light-spacing-5 variables.$global-light-spacing-4;
  width: 100%;
}

.pmc-card-text {
  @include flex(column, variables.$global-light-spacing-1);
}

.pmc-card-buttons-container {
  @include flex(column, variables.$global-light-spacing-1);
}

.pmc-card-img-container {
  // max-width: 48px;
  display: flex;
  height: 14px;
  max-height: 14px;
}

.qr-btn-no-text {
  display: none;
}

.pm-terms-and-conditions {
  max-width: 780px;

  p {
    line-height: 20px;
  }
}

// === MEDIA QUERIES ===
@media (max-width: 740px) {
  .pmc-cards-container {
    flex-direction: column;
  }

  .division-line {
    height: 1px;
    width: 100%;
    background-color: var(
      --secondaryColor2,
      variables.$global-light-gray-gray-4
    );
  }

  .pmc-card {
    border: none;
    padding: variables.$global-light-spacing-2 0px;
  }
}

@media (max-width: 680px) {
  .pt-left {
    gap: variables.$global-light-spacing-1;
  }

  .pt-left h4 {
    font-size: variables.$global-light-font-size-3;
  }

  .pt-icon {
    transform: scale(1);
  }
}

@media (max-width: 420px) {
  .page-title {
    padding: variables.$global-light-spacing-3 variables.$global-light-spacing-2;
  }

  .payment-method-outer-container {
    padding: variables.$global-light-spacing-2;
    gap: variables.$global-light-spacing-2;
  }

  .payment-method-card {
    border-radius: 12px;
    padding: variables.$global-light-spacing-4 variables.$global-light-spacing-3;
    gap: variables.$global-light-spacing-2;
  }

  .division-line {
    margin: variables.$global-light-spacing-0 0px;
  }

  h5 {
    font-size: variables.$global-light-font-size-3;
  }

  .pmc-card-buttons-container {
    flex-direction: row;
  }

  .pmc-card-buttons-container .btn-flat {
    width: 100%;
  }

  .btn-flat-secondary {
    display: none;
  }

  .qr-btn-no-text {
    display: block;
  }

  .mat-mdc-unelevated-button > .mat-icon {
    margin: 0px;
  }
}

@container main-layout (max-width: 640px) {
  .pmc-cards-container {
    flex-direction: column;
  }
}
